/* LOGIN PAGE*/
body.not-logged-in.page-user,
body.page-user-login,
body.page-user-password {
  background: url('../images/login-bg.png') #030409 no-repeat top center;

  .main-menu,
  .header__site-name,
  .layout-swap__top.layout-3col__full,
  h1 {
    display: none;
  }

  .logo {
    width: 350px;
    text-align: center;
    padding: 211px 0 0;
    margin: 0 auto;

    img {
      width: 133px;
    }
  }

  .logo-sub {
    width: 350px;
    margin: 0 auto;
    text-align: center;

    img {
      width: 320px;
    }
  }

  .layout-3col {
    padding: 0;
  }



  .layout-3col__full {
    width: 350px;
    float: none;
    background-color: #fff;
    margin: 0 auto 211px;
    padding: 0;

    .tabs {
      font-family: $font-family-serif;
      background-image: none;
      background: #e6e9ee;
      margin: 0;
      padding: 0;

      .tabs__tab {
        border: 0;
        border-radius: 0;
        background: #e6e9ee;
        margin: 0;

        .tabs__tab-link {
          font-size: 18px;
          background: none;
          padding: 6px 1.5em;
          letter-spacing: 0;
        }
      }

      .tabs__tab.is-active {
        background-color: #fff;
      }
    }

    .elx-form-wrapper {
      width: 300px;
      margin: 0 auto;

      input#edit-name,
      input#edit-pass {
        color: #040a2b;
        font-size: 18px;
        border: 1px solid #ccc;
        padding: 10px 15px;
        box-shadow: inset 0 0 16px 0 rgba(204, 204, 204, .6);
      }

      #edit-submit {
        width: 100%;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: .25em;
        color: #fff;
        background-color: #040a2b;
        border: 0;
        margin: 25px 0;
        padding: 7px 0;
      }
    }
  }
}
